*,
*:before,
*:after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  font-size: 16px;
}

img,
picture,
svg,
video {
  display: block;
  max-width: 100%;
}

@font-face {
  font-family: robotoRegular;
  src: url(../assets/fonts/Roboto-Regular.ttf);
}
@font-face {
  font-family: robotoBold;
  src: url(../assets/fonts/Roboto-Bold.ttf);
}
* {
  font-family: "robotoRegular", sans-serif;
}

.signup-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.svg-container {
  width: 100%;
}
.svg-container .image-container {
  width: 100%;
}

.pitch-input {
  width: 100%;
  padding: 3.5vh 2.75vh 3vh 2.75vh;
  color: hsl(234, 29%, 20%);
  line-height: 1.5;
}

.heading {
  font-weight: 700;
  font-size: 2.3rem;
  font-family: "robotoBold", sans-serif;
  margin-bottom: 0.25em;
  line-height: 1.75;
}

.byline {
  margin-bottom: 1.5em;
}

.offer ul {
  margin-bottom: 2.35em;
}

.list-element {
  display: flex;
  line-height: 1.5;
  align-items: flex-start;
  justify-content: flex-start;
  margin-bottom: 0.6em;
}

.list-element::marker {
  content: "";
}

.list-tick {
  display: inline;
  content: url(/assets/images/icon-list.svg);
  margin-right: 1rem;
}

.point {
  display: inline-block;
}

.contact-details {
  max-width: 470px;
}

.label-and-validation-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.75em;
  font-size: 0.7em;
  font-family: "robotoBold", sans-serif;
}

.label-and-validation-container p {
  color: hsl(4, 100%, 67%);
}

input,
button {
  height: 56px;
  display: block;
  width: 100%;
  border-radius: 10px;
}

input:hover {
  cursor: pointer;
}

.contact-details form button:hover {
  cursor: pointer;
  background: hsl(4, 100%, 67%);
}

.contact-details input {
  border: solid 1px hsl(231, 7%, 60%);
  font-size: 0.95em;
  padding: 0 1.5em;
  margin-bottom: 1.62em;
}

.contact-details button {
  color: hsl(0, 0%, 100%);
  background-color: hsl(234, 29%, 20%);
  font-size: 1em;
  border: none;
  margin-bottom: 0.75em;
}

.success-container {
  padding: 0 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100vh;
}

.thanks {
  height: 70%;
  padding-top: 35%;
}

.thanks-heading {
  font-size: 2.3em;
  padding-top: 15%;
  line-height: 1;
}

.message {
  padding-top: 6%;
  line-height: 1.5;
}

.subscriber-email {
  font-weight: 700;
}

.btn-wrapper {
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 12.5%;
}

.return {
  color: hsl(0, 0%, 100%);
  background-color: hsl(234, 29%, 20%);
  font-size: 0.9em;
  font-weight: 700;
  border: none;
}

.return:hover {
  cursor: pointer;
  background: hsl(4, 100%, 67%);
}

@media screen and (min-width: 600px) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: hsl(234, 29%, 20%);
  }
  .signup-container {
    flex-direction: row-reverse;
    background-color: white;
    border-radius: 2.25rem;
    max-height: 641px;
    max-width: 928px;
    padding: 3.5vh 2.75vh 3vh 2.75vh;
  }
  .svg-container {
    max-width: 400px;
  }
  .heading {
    font-size: 3.5rem;
    letter-spacing: -2px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 1.65rem;
  }
  .pitch-input {
    width: 100%;
    padding: 3.5vh 7vh 3vh 4vh;
    color: hsl(234, 29%, 20%);
    line-height: 1.5;
  }
  .success-container {
    padding: 0 4rem;
    background-color: white;
    max-width: 504px;
    max-height: 520px;
    border-radius: 2rem;
  }
  .thanks {
    padding-top: 10%;
  }
  .thanks-heading {
    font-size: 3.75em;
    padding-top: 12.5%;
    line-height: 1;
  }
  .btn-wrapper {
    padding-bottom: 17.5%;
  }
}